import { Plugin, CangjieClipboardEvent, Controller } from '@ali/4ever-cangjie';

export default function createOnCangjiePaste(onCangjiePaste?: Plugin['onCangjiePaste']) {
  return (
    event: CangjieClipboardEvent,
    controller: Controller,
    next: () => unknown,
  ): unknown => {
    if (onCangjiePaste) return onCangjiePaste(event, controller, next);
    return next();
  };
}
